import { IUser, ICallApiRequest } from "../types/auth";

export const LOGIN = "LOGIN";
export type LOGIN = typeof LOGIN;

export interface ILoginAction {
  type: LOGIN;
  payload: ICallApiRequest;
}

export const loginAction = (request: ICallApiRequest): ILoginAction => {
  return {
    type: LOGIN,
    payload: request
  };
};

export const SET_USER_PROFILE = "SET_USER_PROFILE";
export type SET_USER_PROFILE = typeof SET_USER_PROFILE;

export interface ISetUserAction {
  type: SET_USER_PROFILE;
  payload: IUser;
}
export const setUserAction = (user: IUser): ISetUserAction => {
  return {
    type: SET_USER_PROFILE,
    payload: user
  };
};
