import { ICallApiRequest } from "../types/auth";
import { PATH } from "../constants";
import { REACT_APP_API_URL } from "./environment";
import { history } from "../";

export const loginAction = async (reqBody: ICallApiRequest) => {
  console.log(process.env);
  localStorage.setItem("token", "qrewqsdfwe123asd3xzc");

  const request = new Request(`${REACT_APP_API_URL}${reqBody.url}`, {
    method: reqBody.method,
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
      "Access-Control-Allow-Methods": "POST, OPTIONS"
    },
    body: JSON.stringify(reqBody.params)
  });

  const response = await fetch(request);

  if (response) {
    const user = await response.json();
    if (user && user.data) {
      localStorage.setItem("refresh", user.data.refresh);
      localStorage.setItem("access", user.data.access);
      history.push(PATH.recordList);
      return true;
    }
  }
  return false;
};

export const logoutAction = () => {
  localStorage.removeItem("refresh");
  localStorage.removeItem("access");
  history.push(PATH.login);
};
